<template>
  <div class="list">
    <h1>상세페이지 목록</h1>
    <v-btn color="success"><router-link to="/write">글쓰기</router-link></v-btn>
      <v-layout row>
    <v-flex xs12 sm6 offset-sm3>
      <v-card>
        <v-list v-for="list in lists" :key="list.num" two-line subheader>
          <v-list-tile avatar >
            <v-list-tile-avatar>
              <v-icon class="blue white--text">assignment</v-icon>
            </v-list-tile-avatar>

            <v-list-tile-content>
              <v-list-tile-title v-html="list.title"></v-list-tile-title>
            </v-list-tile-content>
              <v-btn color="success" @click="copy(list)">복사</v-btn>
              <v-btn color="info"><router-link :to="'/update/'+list.num">수정</router-link></v-btn>
              <v-btn color="error" @click="deleteDetail(list)">삭제</v-btn>
          </v-list-tile>
          <v-divider></v-divider>
        </v-list>
      </v-card>
    </v-flex>
  </v-layout>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'list',
  data: () => ({
    lists: []
  }),
  created () {
    axios.get('https://todo.bagcw.com/nospo/detail').then( (res) => {
      this.lists = res.data
    })
  },
  methods: {
    updateDetail (list) {
      this.$router.push({path:'/update', params: {id:list.num}})
    },
    deleteDetail (list) {
      axios.delete(`https://todo.bagcw.com/nospo/detail/${list.num}`).then( (res) => {
        if(res.data === 'ok') {
          alert('삭제완료');
          this.$router.push('/')
        }
      })
    },
    copy (list) {
      delete list.num
      console.log(list)
      axios.post('https://todo.bagcw.com/nospo/detail', list).then( (res) => {
        if(res) {
          alert('복사완료')
          this.$router.push('/')
        }
      })
    }
  }
}
</script>
<style>
a {color: #fff; text-decoration: none; outline: none}
 a:hover, a:active {text-decoration: none; color:#fff; background-color:#f59000;}
</style>
